@charset "UTF-8";
.wrapper {
  width: 48%;
}

.member-area {
  width: 100%;
}

.member-row {
  width: 100%;
}

.per-member {
  cursor: pointer;
  width: 150px;
  height: 150px;
  border-radius: 50%;
  background: #ffffff;
  background: cover;
  position: relative;
}

.per-member-a {
  text-decoration: none;
  z-index: 2;
  position: relative;
}

.active {
  border: 8px solid #9B9BE3;
}

.member-img {
  position: relative;
  border-radius: 50%;
  border: 8px solid #ffffff;
}

.member-img:hover {
  border: 8px solid #1e1c1c;
}

.member-img .pseudo {
  position: absolute;
  content: '';
  transform: translateX(-50%) translateY(50%);
  left: 0;
  top: -50%;
  z-index: 10;
  display: none;
