.ps-show {
  opacity: .8;
  z-index: 10;
}

label {
  position: relative;
}

label input[type="checkbox"] {
  display: none;
}

label .pick {
  position: absolute;
  content: '';
  right: 50%;
  left: 30%;
  bottom: 50%;
  top: 30%;
  color: #ffffff;
  display: none;
  z-index: 20;
}

label .pick:checked {
  display: block;
}

.arr-active {
  display: block;
}

.title-step {
  width: 100%;
}

.d-list-title {
  padding: 30px;
  position: relative;
}

.d-list-title::after {
  position: absolute;
  right: 8px;
  content: "+";
}

.wrapper .slide {
  clear: both;
  width: 100%;
  overflow: hidden;
  text-align: center;
  transition: height .4s ease;
}

.wrapper .slide option {
  padding: 16px 30px;
}
